<template>
  <article class="home">
    <!-- loading -->
    <Loading v-if="!state.isLoaded" />
    <!-- loading -->
    <div id="map" />
    <div class="home_btns">
      <button @click="goCurrentPosition">▼</button>
      <button @click="zoomIn">+</button>
      <button @click="zoomOut">-</button>
    </div>
  </article>
</template>

<script>
import { onMounted, reactive } from "vue";
import api from "@/api";
//components
import Loading from "@/components/Loading";

// 카카오 맵 스크립트
const KAKAO_SCRIPT =
  "https://dapi.kakao.com/v2/maps/sdk.js?autoload=false&appkey=c18742c14562f73324a4c92c7d085dce&libraries=services";

// 현위치 마커 이미지
const HOME_IMAGE =
  "data:image/png;base64,";

// 유치원 마커 이미지
const MARKER_IMAGE =
  "data:image/png;base64,";

export default {
  name: "Home",
  components: {
    Loading,
  },
  setup() {
    const state = reactive({
      isLoaded: false,
      map: null,
      geocoder: null,
      level: 10,
      marker: null,
    });

    onMounted(() => {
      window.kakao && window.kakao.maps ? initMap(10) : addKakaoMapScript();
    });

    const addKakaoMapScript = () => {
      const script = document.createElement("script");
      /* global kakao */
      script.onload = () => kakao.maps.load(initMap);
      script.src = KAKAO_SCRIPT;
      document.head.appendChild(script);
    };

    const initMap = (level) => {
      state.isLoaded = false;
      navigator.geolocation.getCurrentPosition(async (position) => {
        const { latitude, longitude } = position.coords;
        const container = document.getElementById("map");
        state.map = new kakao.maps.Map(container, {
          center: new kakao.maps.LatLng(latitude, longitude),
          level,
        });
        state.geocoder = new kakao.maps.services.Geocoder();
        setMarker(HOME_IMAGE, latitude, longitude);
        // 기본정보 불러오기
        state.basicInfo = await api.getBasicInfo({
          sidoCode: 41,
          sggCode: 41173,
        });
        state.basicInfo.data.kinderInfo.forEach((info) => {
          searchAddr(info.addr);
        });
        state.isLoaded = true;
      });
    };

    // 마커 찍기
    const setMarker = (HOME_IMAGE, lat, lng) => {
      const imageSrc = HOME_IMAGE;
      const imageSize = new kakao.maps.Size(36, 36);
      const imageOption = { offset: new kakao.maps.Point(27, 69) };
      const markerImage = new kakao.maps.MarkerImage(
        imageSrc,
        imageSize,
        imageOption
      );
      const markerPosition = new kakao.maps.LatLng(lat, lng);
      const marker = new kakao.maps.Marker({
        position: markerPosition,
        image: markerImage,
      });

      marker.setMap(state.map);
    };

    // 주소검색
    const searchAddr = (addr) =>
      state.geocoder.addressSearch(addr, (result, status) => {
        // 정상적으로 주소검색이 완료됐으면
        if (status === kakao.maps.services.Status.OK) {
          // 마커 찍기
          setMarker(MARKER_IMAGE, result[0].y, result[0].x);
        }
      });

    // 현위치로
    const goCurrentPosition = () => initMap(3);

    // 줌인
    const zoomIn = () => state.map.setLevel((state.level -= 1));

    // 줌아웃
    const zoomOut = () => state.map.setLevel((state.level += 1));

    return { state, goCurrentPosition, zoomIn, zoomOut };
  },
};
</script>

<style lang="scss" scoped>
.home {
  position: relative;
  width: 100%;
  height: calc(100vh - 10vh);
  #map {
    width: inherit;
    height: inherit;
  }
  .home_btns {
    border-radius: 5px;
    background-color: white;
    position: absolute;
    z-index: 999;
    top: calc(40vh);
    right: 2vw;
    display: flex;
    flex-direction: column;
    button {
      all: initial;
      width: 30px;
      height: 30px;
      text-align: center;
    }
  }
}

@keyframes appear {
  from {
    transform: translateY(-10px);
    opacity: 0;
  }
  to {
    transform: translateY(0);
    opacity: 1;
  }
}
</style>
